import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';


class RegisterForm extends React.Component {
  constructor() {
    super();
    this.state = {
      fields: {},
      errors: {}
    };
    
    this.handleChange = this.handleChange.bind(this);
    this.submituserRegistrationForm = this.submituserRegistrationForm.bind(this);
    
  };
  
  handleChange(e) {
    let fields = this.state.fields;
    fields[e.target.name] = e.target.value;
    this.setState({
      fields
    });
    
  }
  
  submituserRegistrationForm(e) {
    e.preventDefault();
    if (this.validateForm()) {
      let fields = {};
      fields["username"] = "";
      fields["emailid"] = "";
      fields["mobileno"] = "";
      fields["password"] = "";
      this.setState({fields: fields});
      alert("Cпасибо, мы завладели вашими данными");
      console.log("Спасибо, мы завладели вашеми данными");
    }
    
  }
  
  validateForm() {
    
    let fields = this.state.fields;
    let errors = {};
    let formIsValid = true;
    
    if (!fields["username"]) {
      formIsValid = false;
      errors["username"] = "Пожалуйста введите ваше имя.";
    }
    
    if (typeof fields["username"] !== "undefined") {
      if (!fields["username"].match(/^[a-zA-Z,а-яёА-ЯЁ]{3,20}\s[a-zA-Z,а-яёА-ЯЁ]{3,20}/)) {
        formIsValid = false;
        errors["username"] = "Укажите валидное имя. Например Алишер Усманов. Каждое слово минимум по 3 буквы.";
      }
    }
    
    if (!fields["emailid"]) {
      formIsValid = false;
      errors["emailid"] = "Пожалуйста введите ваше email-ID.";
    }
    
    if (typeof fields["emailid"] !== "undefined") {
      //regular expression for email validation
      var pattern = new RegExp('[A-Za-z]{1,20}[@]{1,}[A-Za-z]{2,10}[.]{1}[a-z]{2,4}');
      if (!pattern.test(fields["emailid"])) {
        formIsValid = false;
        errors["emailid"] = "Укажите валидную почту. Например mail@mail.ru";
      }
    }
    
    if (!fields["mobileno"]) {
      formIsValid = false;
      errors["mobileno"] = "Пожалуйста введите номер вашего телефона.";
    }
    
    if (typeof fields["mobileno"] !== "undefined") {
      if (!fields["mobileno"].match('79[0-9]{9}')) {
        formIsValid = false;
        errors["mobileno"] = "Введите правильный телефон. Например 79xxxxxxxxx";
      }
    }
    
    if (!fields["password"]) {
      formIsValid = false;
      errors["password"] = "Пожалуйста введите пароль.";
    }
    
    if (typeof fields["password"] !== "undefined") {
      if (!fields["password"].match('[A-Za-zА-Яа-яЁё,0-9]{8,}')) {
        formIsValid = false;
        errors["password"] = "Разве это пароль? Минимум 8 символов.";
      }
    }
    
    this.setState({
      errors: errors
    });
    return formIsValid;
    
    
  }
  
  
  render() {
    return (
      <div id="main-registration-container">
        <div id="register">
          <form method="post" name="userRegistrationForm" onSubmit={this.submituserRegistrationForm}>
            <label>Имя Фамилия</label>
            <input type="text" name="username" value={this.state.fields.username} onChange={this.handleChange}/>
            <div className="errorMsg">{this.state.errors.username}</div>
            <label>Email</label>
            <input type="text" name="emailid" value={this.state.fields.emailid} onChange={this.handleChange}/>
            <div className="errorMsg">{this.state.errors.emailid}</div>
            <label>Пароль</label>
            <input type="password" name="password" value={this.state.fields.password} onChange={this.handleChange}/>
            <div className="errorMsg">{this.state.errors.password}</div>
            <div id='multipleField'>
              <div className='div-button'>
                <label>Номер телефона</label>
                <input
                  type="tel"
                  name="mobileno"
                  value={this.state.fields.mobileno}
                  onChange={this.handleChange}/>
                <div className="errorMsg">{this.state.errors.mobileno}</div>
                <button className='addAnotherPhone'
                        type="button"
                        value="Добавьте еще один телефон">
                </button>
              </div>
            </div>
            <button type="submit" className="button" value="Зарегистрироваться">Зарегистрироваться</button>
          </form>
        </div>
      </div>
    
    );
  }
  
  
}

ReactDOM.render(<RegisterForm/>, document.getElementById('root'));




